import React, { useEffect, useRef } from 'react';
import Chat from './Chat';
import './ChatWindow.scss';

const ChatWindow = ({ messages }) => {
  const chatList = messages.map(message => <Chat sender={message.sender} message={message.message} key={message.id} />);

  const firstUpdate = useRef(true);

  useEffect(() => {
    if (firstUpdate.current) {
      firstUpdate.current = false;
      return;
    }

    // ë©”ì‹œì§€ ì…ë ¥í•  ë•Œë§ˆë‹¤ ìŠ¤í¬ë¡¤ ë‚´ë¦¬ê¸°
    // ë‚˜ì¤‘ì—ëŠ” ì‚­ì œ ìˆ˜ì •ë„ í•´ì•¼í•´ì„œ ì‚¬ì‹¤ ì´ëŸ° ì‹ìœ¼ë¡œ ì²˜ë¦¬í•˜ë©´ ì•ˆ ë¨
    // ë¶„ëª…íˆ ê³ ì¹˜ê²Œ ë  ê±°ì„
    const chatWindow = document.querySelector('.ChatWindow');
    chatWindow.scrollTop = chatWindow.scrollHeight;
  });

  return (
    <div className="ChatWindow">
      {chatList}
    </div>
  );
};

export default ChatWindow;